import React, { useState } from 'react'
import {useNavigate} from "react-router-dom";
import setCookie from "../../../../functions/setCookie";
import {useDispatch} from "react-redux";
import {setUser} from "../../../../redux/toolkitSlice";

export const ProfileNav = ({ onProfInfo, onProfOrders, onProfBonus }) => {
	const [activeTab, setActiveTab] = useState(' ');

	const handleClickTab = (tabName) => {
		setActiveTab(tabName)
	}

	const navigate = useNavigate()
	const dispatch = useDispatch()

	const handleExit = () => {
		navigate("/")
		setCookie("cookieToken", "")
		dispatch(setUser({}))
	}

	return (
		<ul className="account__aside_nav aside_nav sticky" data-margin-top="30px">
			<li>
				<button
				 	onClick={() => {handleClickTab(' '); onProfInfo();}}
				 	aria-label=" "
					className={activeTab === ' ' ? 'is-current' : ''}
				>
					<span> </span>
					<svg width="20" height="20" viewBox="0 0 20 20">
						<use xlinkHref="#arrow-next"></use>
					</svg>
				</button>
			</li>
			<li>
				<button 
					onClick={() => {handleClickTab(' '); onProfOrders();}} 
					aria-label=" "
					className={activeTab === ' ' ? 'is-current' : ''}
				>
					<span> </span>
					<svg width="20" height="20" viewBox="0 0 20 20">
						<use xlinkHref="#arrow-next"></use>
					</svg>
				</button>
			</li>
			<li>
				<button 
					onClick={() => {handleClickTab('  '); onProfBonus();}} 
					aria-label="  "
					className={activeTab === '  ' ? 'is-current' : ''}
				>
					<span>  </span>
					<svg width="20" height="20" viewBox="0 0 20 20">
						<use xlinkHref="#arrow-next"></use>
					</svg>
				</button>
			</li>
			<li>
				<br/>
				<button onClick={handleExit} aria-label="  ">
					<span></span>
					{/*<svg width="20" height="20" viewBox="0 0 20 20">*/}
					{/*	<use xlinkHref="#arrow-next"></use>*/}
					{/*</svg>*/}
				</button>
			</li>
		</ul>
	)
}
